import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LayoutComponent } from './layout.component';
import { ReporteSedeComponent } from '../reportes/reporte-sede/reporte-sede.component';


const routes: Routes = [
  {
    path: '', component: LayoutComponent, children: [
      { path: '', redirectTo: 'profesor' },
      {
        path: 'profesor',
        loadChildren: () => import('../profesor/profesor.module').then(m => m.ProfesorModule)
      },
      {
        path: 'evento',
        loadChildren: () => import('../evento/evento.module').then(m => m.EventoModule)
      },
      {
        path: 'personas',
        loadChildren: () => import('../personas/personas.module').then(m => m.PersonasModule)
      },
      {
        path: 'reportes',
        loadChildren: () => import('../reportes/reportes.module').then(m => m.ReportesModule)
      },
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class LayoutRoutingModule { }
