import { Component, OnInit, Input, EventEmitter, Output  } from '@angular/core';
import { ActivatedRoute, Router, Event, NavigationStart, NavigationCancel, NavigationError, NavigationEnd } from '@angular/router';

@Component({
  selector: 'app-wrapper',
  templateUrl: './wrapper.component.html',
  styleUrls: ['./wrapper.component.css']
})
export class WrapperComponent implements OnInit {

  public user: any = {name: 'Admin'};
  public currentAccount = null;
  public listAccounts = null;
  public isLoading = false;

  @Output() menuChange = new EventEmitter();
  //@Input() public options: any[];
  public isCollapsed: boolean = false;

  options:Array<Object> = [
    {
      icon: 'user',
      title: 'Profesores',
      url: 'profesor',
      submenu: [
        {
          icon: 'usergroup-add',
          title : 'Crear Profesor',
          url: 'profesor/nuevo'
        },
        {
          icon: 'usergroup-add',
          title : 'Modifica / busca profesor',
          url: 'profesor/lista'
        }
      ]
    },
    {
      icon: 'calendar',
      title: 'Eventos',
      url: 'evento',
      submenu: [
        {
          icon: 'usergroup-add',
          title : 'Crear Evento',
          url: 'evento/nuevo'
        },
        {
          icon: 'usergroup-add',
          title : 'Modifica / busca evento',
          url: 'evento/lista'
        }
      ]
    },
    {
      icon: 'team',
      title: 'Personas',
      url: 'personas',
      submenu: [
        {
          icon: 'user',
          title: 'Carga relación de participantes',
          url: 'personas'
        },
        {
          icon: 'user',
          title: 'Modifica/busca Participante',
          url: 'personas/lista'
        },
        {
          icon: 'user',
          title: 'Reenvío de QR',
          url: 'personas_b'
        },
        {
          icon: 'user',
          title: 'Baja de participante',
          url: 'personas/baja'
        }
      ]
    },
    {
      icon: 'printer',
      title: 'Reportes',
      url: 'reportes',
      submenu: [
        {
          icon: 'user',
          title: 'Reporte por Fecha',
          url: 'reportes'
        },
        {
          icon: 'user',
          title: 'Reporte Detallado',
          url: 'personas_a'
        },
        {
          icon: 'user',
          title: 'Reporte General',
          url: 'personas_a'
        },
      ]
    },
  ];


  constructor(
    private activatedRoute: ActivatedRoute,
    private router: Router
  ) {
   
    this.router.events.subscribe((event: Event) => {
      switch (true) {
        case event instanceof NavigationStart:
          this.isLoading = true;
          break;
        case event instanceof NavigationCancel:
        case event instanceof NavigationError:
        case event instanceof NavigationEnd:
          this.isLoading = false;
          break;
        default:
          break;
      }
    })
  }

  ngOnInit() {
    if(screen.width < 500){
      this.isCollapsed = true;
    }
    this.loadDataComponent();
  }



  private async loadDataComponent() {
    await this.getUserActive();
  }

  private getUserActive() {
    return this.activatedRoute.data.subscribe(res => this.user = res.data );
  }

  public logout(){
    this.isLoading = true;
    sessionStorage.removeItem("USUARIO");
    this.router.navigate(['login']);
    this.isLoading = false;
  }

  public handleCollapse() {
    window.innerWidth < 768 ? this.isCollapsed = true : null;
  }

  public collapseChange() {
    window.innerWidth < 768 ? this.isCollapsed = false : null;
  }

  collapse(){
    this.isCollapsed = !this.isCollapsed;
    console.log(this.isCollapsed)
  }


}
